      title.setAttribute("id", `eyedropper24-${Eyedropper24Directive.titleIdCounter}`);
      svg.appendChild(title);
      svg.setAttribute("aria-labelledby", `eyedropper24-${Eyedropper24Directive.titleIdCounter}`);
